import './Navbar.css';
import Logo from '../../assets/images/Logo.png';
import Menu from '../../assets/icons/menu.png';
import Close from '../../assets/icons/close.png';
import {Link} from "react-router-dom";
import useMedia from "../../customHooks/useMedia";
import {useState} from "react";

const nav_items = ["Home", "IEEE", "Competition", "Sponsors", "Contact"]

const Navbar = ({active}) => {
    const isSmallDevice = useMedia('(max-width: 1100px)');
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    if (active === "") {
        active = "Home"
    }

    return (isSmallDevice ? (<>
        <nav className="navbar-mobile">
            <img className="nav-logo-mobile" src={Logo} alt=""/>
            {!isMenuOpen && <img className="nav-menu-mobile" src={Menu} alt="" onClick={() => setIsMenuOpen(true)}/>}
        </nav>
        {isMenuOpen && (<div
                className="absolute top-0 left-0 w-full h-full bg-black bg-opacity-70 flex justify-center items-center">
                <ul className="navItems-mobile ">
                    <img className="absolute right-8 top-10 h-6" src={Close} alt=""
                         onClick={() => setIsMenuOpen(false)}/>
                    {nav_items.map((item, index) => {
                        return (<li className={active === item ? "active" : undefined} key={index}><Link
                            to={item === "Home" ? "/" : `/${item}`}>{item} </Link></li>)
                    })}
                </ul>
            </div>)}


    </>) : (<nav className="navbar">
            <img className="nav-logo" src={Logo} alt=""/>
            <ul className="navItems">
                {nav_items.map((item, index) => {
                    return (<li className={active === item ? "active" : undefined} key={index}><Link
                        to={item === "Home" ? "/" : `/${item}`}>{item} </Link></li>)
                })}
            </ul>
            <button className="navButton"> Register </button>
        </nav>

    ));
}

export default Navbar;
